html {
    font-size: 16px;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    box-sizing: border-box;
}
:root {
    --spacer: .62rem;
    --lg-ratio: 61.8%;
    --sm-ratio: 38.2%;
    --cl-gray:    #d1d5db;
    --cl-red:     #fca5a5;
    --cl-yellow:  #fcd34d;
    --cl-green:   #6ee7b7;
    --cl-blue:    #93c5fd;
    --cl-indigo:  #a5b4fc;
    --cl-purple:  #c4b5fd;
    --cl-pink:    #fbb6ce;
    --cl-slate:   #94a3b8;
    --cl-emerald: #34d399;
    --cl-teal:    #4fd1c5;
    --cl-cyan:    #6ee7b7;
    --cl-amber:   #fbbf24;
    --cl-lime:    #a3e635;
    --cl-rose:    #f472b6;
    --cl-fuchsia: #d946ef;
    --cl-violet:  #a855f7;
    --cl-sky:     #60a5fa;
    --cl-orange:  #f97316;
    --cl-zinc:    #a1a1aa;
}

* {
    box-sizing: border-box;
    user-select: none;
}

body {
    position: relative;
    display: flex;
    flex-direction: column;
    flex: 1;
    padding: 0;
    margin: 0;
    background-color: #222222;
    color: white;
    font-size: 100%;
    height: 100%;
}

body > header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 1;
    padding: .25rem;
    background-color: #ffffff50;
}

body > main {
    position: relative;
    z-index: 0;
    display: flex;
    justify-content: flex-start;
    padding: calc(4 * var(--spacer));
    background-color: black;
    color: white;
    font-size: 100%;
    height: 100%;
    _min-height: 100svh;
    width: 100%;
    div {
        display: flex;
        margin: var(--spacer);
        padding: calc(2 * var(--spacer));
        border-radius: var(--spacer);
    }
    & > div {
        background-color: var(--cl-blue);
        color: black;
        & > div {
            flex:1;
            background-color: var(--cl-amber);
            & > div {
                flex:1;
                background-color: var(--cl-red);
                & > div {
                    flex:1;
                    background-color: var(--cl-green);
                }
            }
        }
    }
}

.row {
    flex-direction: row;
}
.col {
    flex-direction: column;
}

dialog {
    background-color: #34d399;
    padding: 0;
    form {
        margin: 0;
        display: flex;
        flex-direction: column;
        background-color: pink;
        border: 1px solid black;
        padding: 2rem;
    }
    &::backdrop {
        padding: 0;
        margin: 0;
        background-color: #00000050;
    }
}